* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #008083;
  --primary-darker-color: darker(30%, var(--primary-color));
  --dark-color: #2c3131;
  --grey-color: #a4a9a9;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', 'Helvitica', 'Arial', serif;
}

h1, h2, h3, h4, h5, h6, p, a, span {
  font-family: 'Lato', 'Helvitica', 'Arial', serif;
  font-style: normal;
  text-decoration: none;
}

.container {
  max-width: 90%;
  width: 100%;
  display: block;
  margin: 0 auto;
}

/** SMALLER DISPLAYS BREAKPOINT **/
@media (min-width: 384px) {
  .container {
    max-width: 384px;
  }
}

/** MEDIUM DISPLAYS BREAKPOINT **/
@media (min-width: 900px) {
  .container {
    max-width: 800px;
  }
}

/** LARGE DISPLAYS BREAKPOINT **/
@media (min-width: 1200px) {
  .container {
    max-width: 1175px;
  }
}

.header {
    display: block;
    margin: 0 auto;
    text-align: center;
}

.header img {
    display: block;
    margin: 30px auto;
    max-width: 200px;
    width: 100%;
    border-radius: 50%;
}

.header h1 {
    color: var(--primary-color);
}

.header h2 {
    color: var(--dark-color);
}

.header .location {
    color: var(--dark-color);
    margin: 10px 0;
}

.header p {
    color: var(--grey-color);
}
.skills__section {
    margin: 50px auto;
    max-width: 384px;
    width: 100%;
    display: block;
}
@media (min-width: 900px) {
    .skills__section {
        max-width: 500px;
    }
}

.skills__section h2 {
    color: var(--primary-color);
    text-align: center;
}

.skills__section ul li:last-child {
    grid-area: last
}

.skills__section ul {
    margin: 50px 0;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    align-content: center;
    justify-content: space-around;
    grid-template-areas: 
        ". ."
        ". ."
        ". ."
        ". ."
        ". ."
        ". ."
        ". ."
        ". ."
        "last last"
}

@media (min-width: 900px) {
    .skills__section ul {
        grid-template-columns: repeat(4, 1fr);
        grid-template-areas:
            ". . . ."
            ". . . ."
            ". . . ."
            ". . . ."
            "last last last last" 
    }
}

.skills__section img {
    display: block;
    margin: 0 auto;
    max-width: 64px;
    width: 100%;
}
.quote__section {
}

.quote__section ul {
    display: grid;
    grid-template-columns: 1 1fr;
    grid-gap: 20px;
    justify-content: space-around;
    list-style-type: none;
}

@media (min-width: 900px) {
    .quote__section ul {
        grid-template-columns: repeat(3, 1fr);
    }
}

.quote__section li {
    border-left: 1px solid var(--primary-color);
    width: 100%;
    max-width: 100%;
    padding-left: 10px;
    color: var(--grey-color);
}
.languages__section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px 5px;
    margin: 50px 0 0;
    grid-template-areas: 
    "title title"
    "sub-title sub-title"
}

@media (min-width: 900px) {
    .languages__section {
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 50px;
        justify-content: center;
        grid-template-areas: 
            "title title title"
            "sub-title sub-title sub-title"
    }
    
}

.languages__section h2 {
    grid-area: title;
    text-align: center;
    color: var(--primary-color);
    line-height: 0;
}

.languages__section > h3 {
    grid-area: sub-title;
    text-align: center;
    color: var(--grey-color);
    margin: 0 auto 30px;
    width: 60%;
    display: block
}
.language img {
    display: block;
    margin: 0 auto;
    max-width: 100px;
    width: 100%;
    border-radius: 50%;
}

@media (min-width: 900px) {
    .language img {
        max-width: 140px;
    }
}

.disabled {
    -webkit-filter: opacity(50%);
            filter: opacity(50%);
}

.language h3 {
    margin: 40px 0 10px;
    text-align: center;
    color: var(--dark-color);
}

.language p {
    text-align: center;
    color: var(--grey-color);
}

.projects__section {
    margin: 50px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: 
        "title title";
    grid-gap: 20px;
}

.projects__section h2 {
    grid-area: title;
    text-align: center;
    color: var(--primary-color);
    margin: 0 0 30px;
}
.project img {
    max-width: 100%;
    width: 100%;
    height: auto;
}

.project .line {
    width: 30%;
    height: 0;
    max-width: 100%;
    margin: 20px 0;
    border: 2px solid var(--primary-color);
}

.project h3 {
    margin: 10px 0;
    text-align: left;
}

.project p {
    font-size: 1em;
    font-weight: 300;
    color: var(--grey-color);
}
.social__section {
    margin: 50px 0;
}

.social__section h2 {
    text-align: center;
    margin: 50px 0;
    color: var(--primary-color);
}

.social__section ul {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
}

@media (min-width: 900px) {
    .social__section ul {
        grid-template-columns: repeat(4, 1fr);
    }
}
.social {
}

.social a {
    text-decoration: none;
    font-style: normal;
}

.social a:focus {
    box-shadow: none;
    border: none;
}

.social i {
    text-align: center;
    text-decoration: none;
    font-style: normal;
    display: block;
    margin: 0 auto;
    color: var(--dark-color);
    font-size: 3em;
    margin: 50px 0;
}

.social h3 {
    margin: 10px 0;
    color: var(--grey-color);
    text-align: center;
    text-decoration: none;
    font-style: normal;
    -webkit-transition: 200ms linear color;
    transition: 200ms linear color;
}

.social h3:hover {
    color: var(--primary-color);
}
.footer {
    background: rgb(50, 50, 50);
    max-width: 100%;
    width: 100%;
    margin-top: 50px;
}

.footer ul {
    list-style-type: none;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
}

@media (min-width: 900px) {
    .footer ul {
        grid-template-columns: repeat(4, 1fr);
    }
}

.footer li {
    margin: 40px 0 0px;
    text-align: center;
    color: white;
}

@media (min-width: 900px) {
    .footer li {
        text-align: left;
        margin: 50px 0;
    }
}

.footer h2 {
    color: white;
    margin-bottom: 10px;
}

.footer a {
    color: white;
    text-decoration: none;
    font-style: normal;
    -webkit-transition: 200s linear color;
    transition: 200s linear color;
}


.footer li a {
    color: white;
    text-decoration: none;
    font-style: normal;
    -webkit-transition: 200ms linear color;
    transition: 200ms linear color;
}

.footer li a:hover {
    color: var(--primary-color);
}
.footer .misc {
    display: inline-block;
}

.footer .misc a {
    margin-right: 10px;
    font-size: 2em;
}

.selection {
    padding: 5px;
}
